<template>
	<div>
		<el-dialog v-model="formVisible" :title="formTitle" width="66%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" label-width="$template2.back.add.form.base.labelWidth" :rules="rules">
				<el-row>
					<el-col :span="12">
						<el-form-item label="" prop="kechengmingcheng">
							<el-input class="list_inp" v-model="form.kechengmingcheng" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.kechengmingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item prop="kechengfengmian"
									  label=""
						>
							<uploads
								:disabled="!isAdd||disabledForm.kechengfengmian?true:false"
								action="file/upload"

								tip=""
								:limit="3"
								style="width: 100%;text-align: left;"
								:fileUrls="form.kechengfengmian?form.kechengfengmian:''" 
								@change="kechengfengmianUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="zhuanye">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.zhuanye?true:false"
								v-model="form.zhuanye" 
								placeholder=""
								>
								<el-option v-for="(item,index) in zhuanyeLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="banji">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.banji?true:false"
								v-model="form.banji" 
								placeholder=""
								>
								<el-option v-for="(item,index) in banjiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="kechengleixing">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.kechengleixing?true:false"
								v-model="form.kechengleixing" 
								placeholder=""
								>
								<el-option v-for="(item,index) in kechengleixingLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="kechengdagang">
							<el-input class="list_inp" v-model="form.kechengdagang" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.kechengdagang?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="xueshi">
							<el-input class="list_inp" v-model="form.xueshi" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.xueshi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="shangkeshijian">
							<el-input class="list_inp" v-model="form.shangkeshijian" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.shangkeshijian?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="xuefen">
							<el-input class="list_inp" v-model="form.xuefen" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.xuefen?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="fabushijian">
							<el-date-picker
								class="list_date"
								v-model="form.fabushijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.fabushijian?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="jiaoshigonghao">
							<el-input class="list_inp" v-model="form.jiaoshigonghao" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.jiaoshigonghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="jiaoshixingming">
							<el-input class="list_inp" v-model="form.jiaoshixingming" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.jiaoshixingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="kechengjianjie">
							<el-input v-model="form.kechengjianjie" placeholder="" type="textarea"
							:readonly="!isAdd||disabledForm.kechengjianjie?true:false"
							/>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick"></el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'kechengxinxi'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
		kechengmingcheng : false,
		kechengfengmian : false,
		zhuanye : false,
		banji : false,
		kechengleixing : false,
		kechengjianjie : false,
		kechengdagang : false,
		xueshi : false,
		shangkeshijian : false,
		xuefen : false,
		fabushijian : false,
		jiaoshigonghao : false,
		jiaoshixingming : false,
		storeupnum : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//
	//
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("URL"));
		} else {
			callback();
		}
	}
	const rules = ref({
		kechengmingcheng: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		kechengfengmian: [
		],
		zhuanye: [
		],
		banji: [
		],
		kechengleixing: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		kechengjianjie: [
		],
		kechengdagang: [
		],
		xueshi: [
		],
		shangkeshijian: [
		],
		xuefen: [
		],
		fabushijian: [
		],
		jiaoshigonghao: [
		],
		jiaoshixingming: [
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
	const kechengfengmianUploadSuccess=(e)=>{
		form.value.kechengfengmian = e
	}
	//
	const zhuanyeLists = ref([])
	//
	const banjiLists = ref([])
	//
	const kechengleixingLists = ref([])
	//methods

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			kechengmingcheng: '',
			kechengfengmian: '',
			zhuanye: '',
			banji: '',
			kechengleixing: '',
			kechengjianjie: '',
			kechengdagang: '',
			xueshi: '',
			shangkeshijian: '',
			xuefen: '',
			fabushijian: '',
			jiaoshigonghao: '',
			jiaoshixingming: '',
			storeupnum: '0',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.fabushijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='kechengmingcheng'){
					form.value.kechengmingcheng = row[x];
					disabledForm.value.kechengmingcheng = true;
					continue;
				}
				if(x=='kechengfengmian'){
					form.value.kechengfengmian = row[x];
					disabledForm.value.kechengfengmian = true;
					continue;
				}
				if(x=='zhuanye'){
					form.value.zhuanye = row[x];
					disabledForm.value.zhuanye = true;
					continue;
				}
				if(x=='banji'){
					form.value.banji = row[x];
					disabledForm.value.banji = true;
					continue;
				}
				if(x=='kechengleixing'){
					form.value.kechengleixing = row[x];
					disabledForm.value.kechengleixing = true;
					continue;
				}
				if(x=='kechengjianjie'){
					form.value.kechengjianjie = row[x];
					disabledForm.value.kechengjianjie = true;
					continue;
				}
				if(x=='kechengdagang'){
					form.value.kechengdagang = row[x];
					disabledForm.value.kechengdagang = true;
					continue;
				}
				if(x=='xueshi'){
					form.value.xueshi = row[x];
					disabledForm.value.xueshi = true;
					continue;
				}
				if(x=='shangkeshijian'){
					form.value.shangkeshijian = row[x];
					disabledForm.value.shangkeshijian = true;
					continue;
				}
				if(x=='xuefen'){
					form.value.xuefen = row[x];
					disabledForm.value.xuefen = true;
					continue;
				}
				if(x=='fabushijian'){
					form.value.fabushijian = row[x];
					disabledForm.value.fabushijian = true;
					continue;
				}
				if(x=='jiaoshigonghao'){
					form.value.jiaoshigonghao = row[x];
					disabledForm.value.jiaoshigonghao = true;
					continue;
				}
				if(x=='jiaoshixingming'){
					form.value.jiaoshixingming = row[x];
					disabledForm.value.jiaoshixingming = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.storeupnum='0'
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('jiaoshigonghao')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.jiaoshigonghao = json.jiaoshigonghao
				disabledForm.value.jiaoshigonghao = true;
			}
			if(json.hasOwnProperty('jiaoshixingming')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.jiaoshixingming = json.jiaoshixingming
				disabledForm.value.jiaoshixingming = true;
			}
		})
		context?.$http({
			url: `option/zhuanye/zhuanye`,
			method: 'get'
		}).then(res=>{
			zhuanyeLists.value = res.data.data
		})
		context?.$http({
			url: `option/banji/banji`,
			method: 'get'
		}).then(res=>{
			banjiLists.value = res.data.data
		})
		kechengleixingLists.value = ",".split(',')
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save=()=>{
		if(form.value.kechengfengmian!=null) {
			form.value.kechengfengmian = form.value.kechengfengmian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								emit('formModelChange')
								context?.$toolUtil.message(``,'success',()=>{
									formVisible.value = false
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						emit('formModelChange')
						context?.$toolUtil.message(``,'success',()=>{
							formVisible.value = false
						})
					})
				}
			}
		})
	}
	//
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		border: 0px solid #ddd;
		border-radius: 4px;
		padding: 30px;
		margin: 0;
		background: #fff;
		// form item
		:deep(.el-form-item) {
			margin: 0 20px 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 white-space: nowrap;
			 background: none;
			 font-weight: 500;
			 display: block;
			 width: auto;
			 text-align: right;
			 min-width: 100px;
			}
			// 
			.el-form-item__content {
				display: flex;
				width: calc(100% - 120px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 
				.list_inp {
					border: 4px solid #b9c8d2;
					border-radius: 0px;
					padding: 0 10px;
					background: #fff;
					width: auto;
					line-height: 32px;
					box-sizing: border-box;
					min-width: 200px;
					height: 40px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//
				.list_date {
					border: 4px solid #b9c8d2;
					border-radius: 0px;
					background: #fff;
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 200px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
				// 
				.list_sel {
					border: 4px solid #b9c8d2;
					border-radius: 0px;
					padding: 0 10px;
					background: #fff;
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 200px;
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
							.el-input__wrapper{
								border: none;
								box-shadow: none;
								background: none;
								border-radius: 0;
								height: 100%;
								padding: 0;
							}
							.is-focus {
								box-shadow: none !important;
							}
						}
					}
				}
				// 
				.el-textarea__inner {
					border: 4px solid #b9c8d2;
					border-radius: 0;
					padding: 12px 24px;
					color: #333;
					background: #fff;
					width: 100%;
					font-size: 14px;
					min-height: 120px;
				}
				//
				.el-upload-list  {
					//
					.el-upload__tip {
						margin: 7px 0 0;
						color: #999;
						display: flex;
						font-size: 14px;
						justify-content: flex-start;
						align-items: center;
					}
					//
					.el-upload--picture-card {
						border: 4px solid #b9c8d2;
						cursor: pointer;
						background-color: #fff;
						border-radius: 0px;
						width: 120px;
						line-height: 60px;
						text-align: center;
						height: 60px;
						//
						.el-icon{
							color: #999;
							font-size: 26px;
						}
					}
					.el-upload-list__item {
						border: 4px solid #b9c8d2;
						cursor: pointer;
						background-color: #fff;
						border-radius: 0px;
						width: 120px;
						line-height: 60px;
						text-align: center;
						height: 60px;
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		padding: 20px 0 20px 150px;
		display: flex;
		width: 100%;
		justify-content: flex-start;
		align-items: center;
		.formModel_cancel {
			border: 0;
			cursor: pointer;
			border-radius: 20px;
			padding: 0 30px;
			margin: 0 20px 0 0;
			color: #fff;
			background: linear-gradient(180deg, rgba(255,198,201,1) 0%, rgba(190,105,109,1) 100%);
			width: auto;
			font-size: 16px;
			min-width: 120px;
			height: 45px;
