<template>
	<div>
		<div class="register_view">
			<el-form :model="registerForm" class="register_form">
				<div class="title_view">{{projectName}}</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.banzhurenzhanghao" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.mima" 
						 placeholder=""
						 type="password"
						 />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp" v-model="registerForm.mima2" type="password" placeholder="" />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.banzhurenxingming" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<div :style='{"width":"100%","margin":"0 0 0 0px","flex":"1"}' class="list_file_list">
						<uploads
							action="file/upload" 
							tip="" 
							:limit="3"
							:fileUrls="registerForm.touxiang?registerForm.touxiang:''" 
							@change="touxiangUploadSuccess">
						</uploads>
					</div>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.lianxidianhua" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-select 
						class="list_sel"
						v-model="registerForm.xingbie" 
						placeholder=""
						>
						<el-option v-for="item in banzhurenxingbieLists" :label="item" :value="item"></el-option>
					</el-select>
				</div>

				<div class="list_btn">
					<el-button class="register" type="success" @click="handleRegister"></el-button>
					<div class="r-login" @click="close"></div>
				</div>
			</el-form>
		</div>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		nextTick,
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context?.$project.projectName
	//
	import { useRoute } from 'vue-router';
	const route = useRoute()
	const tableName = ref('banzhuren')
	
	//
	const getUUID=()=> {
		return new Date().getTime();
	}
	const registerForm = ref({
        xingbie: '',
	})
	const banzhurenxingbieLists = ref([])
	const init=()=>{
		banzhurenxingbieLists.value = ",".split(',')
	}
    const touxiangUploadSuccess=(fileUrls)=> {
        registerForm.value.touxiang = fileUrls;
    }
	// 
	//
	const handleRegister = () => {
		let url = tableName.value +"/register";
		if((!registerForm.value.banzhurenzhanghao)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if((!registerForm.value.mima)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.mima!=registerForm.value.mima2){
			context?.$toolUtil.message('','error')
			return false
		}
		if((!registerForm.value.banzhurenxingming)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.touxiang!=null){
			registerForm.value.touxiang = registerForm.value.touxiang.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if(registerForm.value.lianxidianhua&&(!context?.$toolUtil.isMobile(registerForm.value.lianxidianhua))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		
		context?.$http({
			url:url,
			method:'post',
			data:registerForm.value
		}).then(res=>{
			context?.$toolUtil.message('','success', obj=>{
				context?.$router.push({
					path: "/login"
				});
			})
		})
	}
	//
	const close = () => {
		context?.$router.push({
			path: "/login"
		});
	}
	init()
</script>
<style lang="scss" scoped>
	
	.register_view {
		background-repeat: no-repeat;
		flex-direction: column;
		background-size: cover!important;
		background: url(http://clfile.zggen.cn/20240418/1d5af3fafbac495f9ddfd914e1b5a496.jpg);
		display: flex;
		min-height: 100vh;
		justify-content: center;
		align-items: center;
		position: relative;
		background-position: center center;
		// 
		.register_form {
			border: 22px ridge #eee;
			border-radius: 0px;
			padding: 0 20px 20px 0;
			margin: 0 auto;
			flex-direction: column;
			background: linear-gradient(180deg, rgba(193,239,255,1) 0%, rgba(41,139,161,1) 100%);
			display: flex;
			width: 688px;
			justify-content: flex-start;
			flex-wrap: wrap;
		}
		// 
		.title_view {
			padding: 0px;
			margin: 30px auto 30px;
			text-shadow: 2px 4px 3px #3a7a9060;
			color: #3a7a90;
			font-weight: 600;
			letter-spacing: 1px;
			width: 90%;
			font-size: 24px;
			text-align: center;
		}
		// item
		.list_item {
			margin: 0  auto 20px;
			display: flex;
			width: 90%;
			justify-content: flex-start;
			align-items: center;
			// label
			.list_label {
				color: #333;
				background: none;
				width: 120px;
				font-size: 14px;
				line-height: 36px;
				box-sizing: border-box;
				text-align: right;
			}
			// 
			:deep(.list_inp) {
				border: 1px solid #eee;
				border-radius: 30px;
				padding: 0 10px;
				color: #666;
				background: #fff;
				flex: 1;
				width: 100%;
				line-height: 38px;
				box-sizing: border-box;
				height: 38px;
				//
				.el-input__wrapper{
					border: none;
					box-shadow: none;
					background: none;
					border-radius: 0;
					height: 100%;
					padding: 0;
				}
				.is-focus {
					box-shadow: none !important;
				}
			}
		}
		//
		:deep(.list_sel) {
			border: 1px solid #eee;
			border-radius: 30px;
			padding: 0 10px;
			color: #666;
			background: #fff;
			flex: 1;
			width: 100%;
			line-height: 38px;
			box-sizing: border-box;
			//
			.select-trigger{
				height: 100%;
				.el-input{
					height: 100%;
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
			}
		}
		//
		.list_btn {
			margin: 10px auto;
			display: flex;
			width: 90%;
			align-items: center;
			flex-wrap: wrap;
			//
			.register {
					border: 0px solid rgba(41,139,161,1);
					border-radius: 30px;
					padding: 0 20px;
					margin: 0 auto;
					color: #333;
					background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(229,248,255,1) 75%, rgba(186,237,255,1) 100%);
					width: auto;
					font-size: 16px;
					min-width: 130px;
					height: 50px;
			}
			//
			.register:hover {
				border: 1px solid rgba(41,139,161,1);
				border-radius: 30px;
				padding: 0 24px;
				margin: 0 auto;
				color: rgba(41,139,161,1);
				background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(229,248,255,1) 75%, rgba(186,237,255,1) 100%);
				width: auto;
				font-size: 16px;
				min-width: 130px;
				height: 50px;
			}
			//
			.r-login {
				cursor: pointer;
				padding: 10px 0 0;
				color: #fff;
				width: 100%;
				font-size: 14px;
				text-align: right;
			}
		}
		//
		.list_file_list  {
			//
			:deep(.el-upload__tip){
				margin: 7px 0 0;
				color: #666;
				display: flex;
				font-size: 14px;
				justify-content: flex-start;
				align-items: center;
			}
			//
			:deep(.el-upload--picture-card){
				border: 1px solid #eee;
				cursor: pointer;
				background-color: #fff;
				border-radius: 30px;
				width: 100px;
				line-height: 68px;
				text-align: center;
				height: 60px;
				//
				.el-icon{
					color: #654B3C;
					font-size: 26px;
				}
			}
			:deep(.el-upload-list__item) {
				border: 1px solid #eee;
				cursor: pointer;
				background-color: #fff;
				border-radius: 30px;
				width: 100px;
				line-height: 68px;
				text-align: center;
				height: 60px;
			}
		}
	}
</style>